body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  color: #434343;
}
.Section-width {
  max-width: 900px;
  margin: 0 auto;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom: 25px;
}
.Section-title {
  font-family: 'Lobster', cursive;
}
.Header-title {
  font-family: 'Lobster', cursive;
  font-size: 30px;
  margin: 0;
}
.Personal-image-profile {
  min-width: 250px;
  max-width: 250px;
  margin: 0 auto;
}
.Personal-image-profile img {
  width: 100%;
}
.Personal-text-intro {
  padding: 20px;
  font-family: 'Open Sans', sans-serif;
}
.Personal-contact p a {
  color: black;
}
.Personal-contact .Personal-social-network {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
}
.Personal-contact .Personal-social-network div a {
  color: black;
}
.Personal-contact .Personal-social-network div a i {
  margin-right: 15px;
  font-size: 30px;
}
@media (min-width: 480px) {
  .Personal-intro {
    display: -webkit-flex;
    display: flex;
  }
  .Personal-image-profile {
    min-width: 128px;
    max-width: 128px;
  }
}
.Skills {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}
.skill {
  font-family: 'Open Sans', sans-serif;
}
.Job {
  font-family: 'Open Sans', sans-serif;
}
.Job .Job-company .Job-company-name {
  margin-bottom: 0px;
}
.Job .Job-company .Job-company-title {
  margin-top: 0px;
  font-size: 13px;
  color: #666666;
}
.Job .Job-stack {
  color: #000000;
}
.Job .Job-description {
  background-color: rgba(0, 0, 0, 0.05);
}
.Education-name,
.Education-title {
  font-family: 'Open Sans', sans-serif;
}

.Interests,
.Updated {
  font-family: 'Open Sans', sans-serif;
}
