.Personal-image-profile {
  min-width: 250px;
  max-width: 250px;
  margin: 0 auto;
}
.Personal-image-profile img {
  width: 100%;
}
.Personal-text-intro {
  padding: 20px;
  font-family: 'Open Sans', sans-serif;
}
.Personal-contact p a {
  color: black;
}
.Personal-contact .Personal-social-network {
  display: flex;
  justify-content: space-between;
  padding-left: 30px;
  padding-right: 30px;
}
.Personal-contact .Personal-social-network div a {
  color: black;
}
.Personal-contact .Personal-social-network div a i {
  margin-right: 15px;
  font-size: 30px;
}
@media (min-width: 480px) {
  .Personal-intro {
    display: flex;
  }
  .Personal-image-profile {
    min-width: 128px;
    max-width: 128px;
  }
}